import { useTranslation } from "react-i18next";
import { useLocation, useNavigate } from "react-router-dom";
import UserMemberContentWrapper from "../../Ui/UserMember/UserMemberContentWrapper";
import UserMemberContainerWrapper from "../../Ui/UserMember/UserMemberContainerWrapper";
import style from "../../UserStaffMember/FinanceManager/FinanceManager.module.css";
import BorderBottom from "../../Ui/Border/BorderBottom";
import {
  useFeedbackDetailStudentFeedback,
  useFeedbackGiveStudentFeedback,
} from "../../Hooks/ApiHooks/InstituteFeedback/institute-feedback-api";
import Loading from "../../Loader/Loading";
import { useEffect, useState } from "react";
import CreateButton from "../../Ui/Button/CreateButton";
import QvipleLoader from "../../Loader/QvipleLoader";
import styleIF from "../../InstituteMember/InstituteFeedback/InstituteFeedback.module.css";

const GiveStudentFeedback = () => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const navigate = useNavigate();
  const [state, setState] = useState([]);
  const [disabled, setDisabled] = useState(false);

  const [feedbackGiveStudentFeedback] = useFeedbackGiveStudentFeedback();
  const {
    feedbackDetailStudentFeedback,
    feedbackDetailStudentFeedbackLoading,
  } = useFeedbackDetailStudentFeedback({
    data: {
      ifid: getQuery.state?.ifid,
    },
    skip: !getQuery.state?.ifid,
  });

  useEffect(() => {
    if (feedbackDetailStudentFeedback?._id) {
      let que = [];
      for (let qu of feedbackDetailStudentFeedback?.questions) {
        let obj = {
          question_sno: qu?.question_sno,
          question_title: qu?.question_title,
          options: [],
        };
        for (let ot of qu?.options) {
          obj.options?.push({
            option_sno: ot?.option_sno,
            option: ot?.option,
            selected: ot?.selected,
          });
        }
        que.push(obj);
      }
      setState(que);
    }
  }, [feedbackDetailStudentFeedback?._id]);

  const onSelect = (index, optIndex) => {
    const inputarr = [...state];
    for (let ot of inputarr[index].options) ot.selected = false;
    inputarr[index].options[optIndex].selected = true;
    setState(() => inputarr);
    console.info("sdff", inputarr);
  };

  const onSave = () => {
    if (
      getQuery.state?.ifid &&
      getQuery.state?.staffId &&
      getQuery.state?.sid &&
      getQuery.state?.notifyId
    ) {
      setDisabled((pre) => !pre);
      feedbackGiveStudentFeedback({
        sid: getQuery.state?.sid,
        giveFeedback: {
          ifid: getQuery.state?.ifid,
          staffId: getQuery.state?.staffId,
          notifyId: getQuery.state?.notifyId,
          questions: state,
        },
      })
        .then(() => {
          setDisabled((pre) => !pre);
          navigate(-1);
        })
        .catch(() => {
          setDisabled((pre) => !pre);
        });
    }
  };
  const onBack = () => {
    navigate(-1);
  };
  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <h6>
              {t("feedback_name")}
              {": "}
              {feedbackDetailStudentFeedback?.feedback_name ?? ""}
            </h6>
            <div className={style.fm_search}>
              <CreateButton
                label={t("save")}
                customStyle={{
                  margin: "0",
                  padding: "0.2rem 1rem",
                }}
                onAction={onSave}
              />
              <img
                src="/images/close-post-icon.svg"
                onClick={onBack}
                alt="close"
                style={{
                  height: "1.2rem",
                  cursor: "pointer",
                }}
              />
            </div>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper>
          {state?.map((que, index) => (
            <section
              key={`question${que?.question_sno}`}
              className={styleIF.ifq_card}
            >
              <div className={styleIF.ifq_card_inner}>
                <h6>
                  <span
                    style={{
                      color: "var(--color-input-label)",
                    }}
                  >{`${t("question")} ${que?.question_sno}: `}</span>
                  {que?.question_title ?? ""}
                </h6>
              </div>

              <div className={styleIF.ifqo_cantainer_student}>
                {que?.options?.map((opt, optIndex) => (
                  <h6
                    key={`option${opt?.option_sno}${optIndex}`}
                    onClick={() => onSelect(index, optIndex)}
                    className={opt?.selected ? styleIF.ifqocs_active : ""}
                  >
                    {opt?.option ?? ""}
                  </h6>
                ))}
              </div>
            </section>
          ))}
          {disabled && <QvipleLoader />}

          {feedbackDetailStudentFeedbackLoading && <Loading />}
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
    </>
  );
};

export default GiveStudentFeedback;
